'use client';

import React, { useState, useEffect, useTransition } from 'react';
import { useActionState } from 'react';
import { uploadFile, type UploadResponse, deleteFileAction, type DeleteResponse } from '@/app/actions';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Textarea } from '@/components/ui/textarea';
import { Alert, AlertDescription, AlertTitle } from '@/components/ui/alert';
import { Card, CardContent, CardHeader, CardTitle, CardDescription } from '@/components/ui/card';
import { Label } from '@/components/ui/label';
import { UploadCloud, AlertCircle, CheckCircle, Copy, ExternalLink, LogOut, FileJson, Info, HelpCircle } from 'lucide-react';
import { useToast } from '@/hooks/use-toast';
import Link from 'next/link';
import { Accordion, AccordionContent, AccordionItem, AccordionTrigger } from '@/components/ui/accordion';

const initialUploadState: UploadResponse = { success: false };
const initialDeleteState: DeleteResponse = { success: false };

interface UploadClientPageProps {
  onUploadSuccess?: (fileId: string, fileName: string) => void;
  onDeleteSuccess?: () => void;
  latestFileId?: string | null;
  latestFileName?: string | null;
}

export default function UploadClientPage({
  onUploadSuccess,
  onDeleteSuccess,
  latestFileId: initialLatestFileId,
  latestFileName: initialLatestFileName
}: UploadClientPageProps) {
  const [uploadFormState, uploadFormAction, isUploadPending] = useActionState(
    uploadFile,
    initialUploadState
  );
  const [deleteFormState, deleteFormAction, isDeletePending] = useActionState(deleteFileAction, initialDeleteState);
  const [isTransitionPending, startTransition] = useTransition();


  const [currentFileId, setCurrentFileId] = useState<string | null>(initialLatestFileId || null);
  const [currentFileName, setCurrentFileName] = useState<string | null>(initialLatestFileName || null);
  const [cookieData, setCookieData] = useState('');
  const [fileName, setFileName] = useState('');
  
  const { toast } = useToast();

  useEffect(() => {
    if (uploadFormState.error && !isUploadPending) {
      toast({
        title: 'Sharing Failed',
        description: uploadFormState.error,
        variant: 'destructive',
      });
    } else if (uploadFormState.success && uploadFormState.fileId && uploadFormState.fileName && !isUploadPending) {
      toast({
        title: 'Cookies Shared!',
        description: `Your cookie data (${uploadFormState.fileName}) has been shared.`,
        variant: 'default',
      });
      setCurrentFileId(uploadFormState.fileId);
      setCurrentFileName(uploadFormState.fileName);
      setCookieData(''); 
      setFileName(''); 
      if (onUploadSuccess) {
        onUploadSuccess(uploadFormState.fileId, uploadFormState.fileName);
      }
    }
  }, [uploadFormState, toast, onUploadSuccess, isUploadPending]);

  useEffect(() => {
    if (deleteFormState.error && !isDeletePending) {
      toast({
        title: 'Delete Failed',
        description: deleteFormState.error,
        variant: 'destructive',
      });
    } else if (deleteFormState.success && !isDeletePending) {
      toast({
        title: 'Session Cleared',
        description: 'The shared cookie data has been deleted.',
        variant: 'default',
      });
      setCurrentFileId(null);
      setCurrentFileName(null);
      if (onDeleteSuccess) {
        onDeleteSuccess();
      }
    }
  }, [deleteFormState, toast, onDeleteSuccess, isDeletePending]);

  useEffect(() => {
    setCurrentFileId(initialLatestFileId || null);
    setCurrentFileName(initialLatestFileName || null);
  }, [initialLatestFileId, initialLatestFileName]);

  const handleSubmit = (event: React.FormEvent<HTMLFormElement>) => {
    event.preventDefault(); 
    if (!cookieData.trim()) {
      toast({ title: "Input Error", description: "Cookie data cannot be empty.", variant: "destructive" });
      return;
    }
    if (!fileName.trim() || !fileName.endsWith('.json')) {
      toast({ title: "Input Error", description: "File name must be provided and end with .json.", variant: "destructive" });
      return;
    }
    startTransition(() => {
        uploadFormAction({ cookieData, fileName });
    });
  };
  
  const isProcessing = isUploadPending || isDeletePending || isTransitionPending;
  const [appBaseUrl, setAppBaseUrl] = useState('');

  useEffect(() => {
    if (typeof window !== 'undefined') {
      setAppBaseUrl(window.location.origin);
    } else {
        setAppBaseUrl(process.env.NEXT_PUBLIC_APP_URL || '');
    }
  }, []);


  return (
    <div className="space-y-6">
      {!currentFileId && (
        <Card className="border-primary/30">
           <CardHeader>
            <div className="flex items-center gap-2 text-primary mb-2">
              <FileJson className="h-6 w-6" />
              <CardTitle className="text-xl">Share Your Apna College Session</CardTitle>
            </div>
            <CardDescription>
             To share your login session, please <strong className="text-primary">manually export your cookies from Apna College</strong> and paste the JSON data below. This application cannot automatically access cookies from other websites.
            </CardDescription>
          </CardHeader>
          <CardContent className="space-y-4">
            <Accordion type="single" collapsible className="w-full">
              <AccordionItem value="item-1">
                <AccordionTrigger>
                  <div className="flex items-center">
                    <HelpCircle className="mr-2 h-4 w-4 text-primary" /> How to Manually Export Cookies from Apna College
                  </div>
                </AccordionTrigger>
                <AccordionContent className="text-sm text-muted-foreground space-y-2">
                  <p className="font-semibold">Important: This web application cannot automatically access your browser's cookies for `apnacollege.in` due to browser security restrictions. You must perform these steps manually.</p>
                  <ol className="list-decimal list-inside space-y-2">
                    <li>
                      <strong>Log in to Apna College:</strong> Ensure you are logged into{' '}
                      <a href="https://www.apnacollege.in" target="_blank" rel="noopener noreferrer" className="text-primary underline hover:text-primary/80">
                        www.apnacollege.in
                      </a> in your web browser.
                    </li>
                    <li>
                      <strong>Open Developer Tools:</strong>
                      <ul className="list-disc list-inside pl-5 text-xs">
                          <li>Right-click anywhere on the Apna College page and select "Inspect" or "Inspect Element".</li>
                          <li>Alternatively, press F12 (Windows/Linux) or Option+Command+I (macOS).</li>
                      </ul>
                    </li>
                    <li>
                      <strong>Navigate to Cookies:</strong>
                      <ul className="list-disc list-inside pl-5 text-xs">
                          <li><strong>Chrome/Edge:</strong> Go to the "Application" tab. In the left sidebar, under "Storage", expand "Cookies" and select `https://www.apnacollege.in`.</li>
                          <li><strong>Firefox:</strong> Go to the "Storage" tab. In the left sidebar, under "Cookies", select `https://www.apnacollege.in`.</li>
                      </ul>
                    </li>
                    <li>
                      <strong>Copy Cookie Data as JSON:</strong>
                      <ul className="list-disc list-inside pl-5 text-xs">
                        <li>The most reliable way is using a browser extension designed for cookie management:
                          <ul className="list-circle list-inside pl-5">
                            <li>Search for extensions like "Cookie-Editor" or "EditThisCookie" in your browser's extension store (Chrome Web Store, Firefox Add-ons, etc.).</li>
                            <li>Install one. Once installed, navigate to `www.apnacollege.in`.</li>
                            <li>Click the extension's icon in your browser toolbar.</li>
                            <li>Look for an "Export" or "Export All" button. Choose "JSON" as the export format if prompted.</li>
                            <li>Copy the exported JSON data to your clipboard.</li>
                          </ul>
                        </li>
                        <li><strong>Manual Alternative (more complex, error-prone):</strong> If not using an extension, you would need to manually identify all relevant cookies for `apnacollege.in` from the developer tools. For each cookie, note its `name`, `value`, `domain`, `path`, `expires` (or if it's a session cookie), `httpOnly`, `secure`, and `sameSite` attributes. Then, you must construct a JSON array of cookie objects. This is highly technical and prone to errors.
                        <p className="mt-1">The JSON should be an array of objects, where each object represents a cookie. Example:</p>
                        <pre className="mt-1 p-2 bg-muted text-xs rounded overflow-x-auto w-full break-words whitespace-pre-wrap">
{`[
  {
    "name": "session_id",
    "value": "some_long_value_string",
    "domain": ".apnacollege.in",
    "path": "/",
    "expires": 1700000000,
    "httpOnly": true,
    "secure": true,
    "sameSite": "Lax"
  },
  {
    "name": "another_cookie",
    "value": "another_value",
    "domain": "www.apnacollege.in",
    "path": "/",
    "session": true, 
    "httpOnly": false,
    "secure": true
  }
]`}
                        </pre>
                        <p className="mt-1">Ensure `domain` is correct (e.g., `.apnacollege.in` or `www.apnacollege.in`). `expires` should be a Unix timestamp in seconds (number of seconds since Jan 1, 1970). If it's a session cookie, some exporters might use `"session": true` or omit `expires`. Include all properties provided by your export method. Using a cookie editor extension automates this correctly.</p>
                        </li>
                      </ul>
                    </li>
                    <li>
                      <strong>Paste Cookie Data:</strong> Paste the copied JSON cookie array into the "Cookie Data (JSON format)" field on this page.
                    </li>
                    <li>
                      <strong>Enter Filename:</strong> Provide a filename for your session (e.g., <code>my_apna_session.json</code>). It must end with <code>.json</code>.
                    </li>
                    <li>
                      <strong>Share:</strong> Click the "Share Manually Copied Cookies" button.
                    </li>
                  </ol>
                </AccordionContent>
              </AccordionItem>
            </Accordion>
            
            <form onSubmit={handleSubmit}>
              <div className="space-y-4">
                <div>
                  <Label htmlFor="cookieData" className="text-sm font-medium">Cookie Data (JSON format)</Label>
                  <Textarea
                    id="cookieData"
                    value={cookieData}
                    onChange={(e) => setCookieData(e.target.value)}
                    placeholder='Paste your manually exported cookie JSON array here. Example: [{"name":"session_id", "value":"...", ...}]'
                    className="mt-1 min-h-[120px] text-xs"
                    disabled={isProcessing}
                    aria-label="Cookie Data in JSON format"
                    required
                  />
                   <p className="text-xs text-muted-foreground mt-1">This data must be manually exported from `apnacollege.in` using browser developer tools or a cookie editor extension.</p>
                </div>
                <div>
                  <Label htmlFor="fileName" className="text-sm font-medium">File Name for Shared Data</Label>
                  <Input
                    id="fileName"
                    type="text"
                    value={fileName}
                    onChange={(e) => setFileName(e.target.value)}
                    placeholder="e.g., apnacollege_session.json"
                    className="mt-1"
                    disabled={isProcessing}
                    aria-label="File Name for the cookie data"
                    required
                    pattern=".*\.json$"
                  />
                  <p className="text-xs text-muted-foreground mt-1">Must end with .json</p>
                </div>
                <Button
                  type="submit"
                  className="w-full bg-accent hover:bg-accent/90 text-accent-foreground"
                  disabled={isProcessing}
                  aria-live="polite"
                >
                  <UploadCloud className="mr-2 h-4 w-4" />
                  {isProcessing ? 'Processing...' : 'Share Manually Copied Cookies'}
                </Button>
              </div>
            </form>
            {uploadFormState.error && !isUploadPending && (
              <Alert variant="destructive" className="mt-4">
                <AlertCircle className="h-4 w-4" />
                <AlertTitle>Sharing Failed</AlertTitle>
                <AlertDescription>{uploadFormState.error}</AlertDescription>
              </Alert>
            )}
          </CardContent>
        </Card>
      )}

      {currentFileId && currentFileName && (
        <Card className="mt-6 bg-secondary/50">
           <CardHeader>
            <div className="flex items-center gap-2 text-green-600">
              <CheckCircle className="h-6 w-6" />
              <CardTitle className="text-xl">Session Currently Shared</CardTitle>
            </div>
          </CardHeader>
          <CardContent className="space-y-4">
            <p className="text-sm text-muted-foreground">
              Shared file: <span className="font-medium break-all">{currentFileName}</span>.
              The receiver can now download this file and manually import the cookies into their browser for `apnacollege.in`.
            </p>

            <div className="space-y-1">
              <Label htmlFor="share-link" className="text-xs font-medium">Shared Session Download Link</Label>
              <div className="flex items-center space-x-2">
                <Input
                  id="share-link"
                  type="text"
                  readOnly
                  value={`${appBaseUrl}/download/${currentFileId}`}
                  className="bg-background text-xs"
                  aria-label="Shared session download link"
                />
                <Button
                  variant="outline"
                  size="icon"
                  onClick={() => copyToClipboard(`${appBaseUrl}/download/${currentFileId}`, toast)}
                  aria-label="Copy link"
                  disabled={isProcessing}
                >
                  <Copy className="h-4 w-4" />
                </Button>
                 <Button variant="outline" size="icon" asChild aria-label="Open link in new tab" disabled={isProcessing}>
                  <Link href={`/download/${currentFileId}`} target="_blank" rel="noopener noreferrer">
                    <ExternalLink className="h-4 w-4" />
                  </Link>
                </Button>
              </div>
            </div>

            <form action={deleteFormAction} className="pt-2">
              <input type="hidden" name="fileId" value={currentFileId} />
              <Button type="submit" variant="destructive" className="w-full" disabled={isProcessing} aria-live="polite">
                <LogOut className="mr-2 h-4 w-4" />
                {isDeletePending ? 'Clearing Session...' : 'Clear Shared Session'}
              </Button>
            </form>
            {deleteFormState.error && !isDeletePending && (
               <Alert variant="destructive" className="mt-2">
                  <AlertCircle className="h-4 w-4" />
                  <AlertTitle>Clearing Failed</AlertTitle>
                  <AlertDescription>{deleteFormState.error}</AlertDescription>
                </Alert>
            )}
             <Alert variant="default" className="mt-4">
                <Info className="h-4 w-4" />
                <AlertTitle>Important</AlertTitle>
                <AlertDescription>Clearing the session will remove the cookie data from the server. Ensure the receiver has downloaded it if needed, as they will need to import the cookies manually into `apnacollege.in`.</AlertDescription>
            </Alert>
          </CardContent>
        </Card>
      )}
    </div>
  );
}

function copyToClipboard(text: string, toastFn: typeof useToast extends () => infer R ? R['toast'] : never) {
  if (typeof navigator !== 'undefined' && navigator.clipboard) {
    navigator.clipboard.writeText(text).then(() => {
      toastFn({ title: 'Link Copied!', description: 'Download link copied to clipboard.' });
    }).catch(err => {
      toastFn({ title: 'Copy Failed', description: 'Could not copy link to clipboard.', variant: 'destructive' });
      console.error('Failed to copy text: ', err);
    });
  } else {
    // Fallback for environments where clipboard API is not available
    try {
      const textArea = document.createElement("textarea");
      textArea.value = text;
      document.body.appendChild(textArea);
      textArea.focus();
      textArea.select();
      document.execCommand('copy');
      document.body.removeChild(textArea);
      toastFn({ title: 'Link Copied! (Fallback)', description: 'Download link copied to clipboard.' });
    } catch (err) {
       toastFn({ title: 'Copy Failed (Fallback)', description: 'Could not copy link to clipboard.', variant: 'destructive' });
      console.error('Fallback copy failed: ', err);
    }
  }
}
